<template>
	<div id="login">
    <div class="header">
      <div class="van-hairline--bottom background_color_ffffff van-nav-bar">
        <div class="van-ellipsis van-nav-bar__title">登录</div>
      </div>
    </div>
    <div class="login_box">
      <div class="login_bar background_color_ffffff">
        <img src="static/img/icon_shouji_zc.png" class="img_top_center width_5"/>
        <input class="font_14 login_input" type="number" placeholder="请输入手机号" v-model="phone"/>
      </div>
      <div class="login_bar background_color_ffffff login_top">
        <img src="static/img/icon_mima_zc.png" class="img_top_center width_5"/>
        <input class="font_14 login_input" type="password" placeholder="请输入密码" v-model="pwd"/>
      </div>
      <div class="login_bar font_12 color_c10804">
        <span @click="toRegister">注册</span>
        <span class="float_right" @click="toForgetPwd">忘记密码?</span>
      </div>
      <div style="margin-top: 30px;" class="text_center">
        <button class="login_button" @click="login">登录</button>
      </div>
      <van-checkbox v-model="checked" checked-color="red" class="next-login">下次直接登录</van-checkbox>
    </div>
	</div>
</template>
<script>
	export default {
		name: 'login',
		data() {
			return {
        phone:'',
        pwd:'',
        checked:false
			}
		},
		methods:{
		  login(){
        let $this = this;
        let obj = {
          phone: $this.phone,
          pwd: $this.pwd
        };
        let qs = require('qs');
        $this.$ajax.post("user/login", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.$toast({
              message: '登录成功',
              position: "bottom"
            });
            if($this.checked){
              localStorage.setItem('nextLogin','1');
            }else{
              localStorage.setItem('nextLogin','2');
            }
            localStorage.setItem('userId',rpdata.data.data.userid);
            $this.$router.push('/');
          }else{
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
          }
        })
        .catch(function(err) {})
      },
      toRegister(){
        this.$router.push('register');
      },
      toForgetPwd(){
        this.$router.push('forgetPwd');
      },
		}
	}
</script>

<style>
  #login .van-checkbox__icon, .van-checkbox__label {
    line-height: 12px !important;
  }
  #login .van-checkbox__icon--checked .van-icon {
    border-color: #c10804 !important;
    background-color: #c10804 !important;
  }
  #login .van-checkbox__icon .van-icon {
    width: 16px;
    height: 16px;
  }
</style>

<style scoped>
  .next-login{
    padding: 60px 20px;
    float: right;
    font-size: 12px;
    color: #8b8b8b;
  }
  .login_button {
    width: 80%;
    height: 40px;
    line-height: 40px;
    border: 0;
    color: #ffffff;
    border-radius: 5px;
    background-color: #c10804;
  }
  .login_input{
    margin-left: 8%;
    border: 0;
  }
  .width_5{
    width: 5%;
  }
  .login_box{
    margin-top: 44px;
  }
  .login_top{
    border-top: 5px solid #eeeeee;
  }
  .login_bar{
    padding: 15px 15px;
  }
</style>



